.productsContainer, .searchResultContainer {
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
}

.productCard {
  border: 1px solid #000;
  min-width: 450px;
  max-width: 400px;
  max-height: 500px;
  margin: 20px;
  padding: 10px;
}

